.link {
    flex-basis: 214px;
    display: flex;
    flex-direction: column;
    border-radius: 40px;
    background-color: #1c1c21;
    padding: 0 20px 0 20px;
    margin-right: 8px;
    /* text-decoration: none; */
    color: #F2F2F3;
    max-width: 856px;
  }
  
  .link:hover {
    cursor: pointer;
  }

  .name {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: #F2F2F3;
  }
  
  .top {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }
  
  .bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }
  
  .image-boxes {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .hidden-count {
    background-color: rgba(28, 28, 33, .5);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }
  
  .image-box {
    width: 56px;
    height: 56px;
    background-color: #131316;
    background-position: center;
    background-size: 112px 56px;
    border: 2px solid var(--colors-interface-accent);
    padding: 2px;
    border-radius: 50%;
    z-index: 6;
  }
  
  .image-box:nth-child(n+2) {
    position: relative;
    left: -10px;
    z-index: 5;
  }
  
  .image-box:nth-child(n+3) {
    position: relative;
    left: -20px;
    z-index: 4;
  }
  
  .image-box:nth-child(n+4) {
    position: relative;
    left: -30px;
    z-index: 3;
  }
  
  .image-box:nth-child(n+5) {
    position: relative;
    left: -40px;
    z-index: 2;
  }
  
  .image-box:nth-child(n+6) {
    position: relative;
    left: -50px;
    z-index: 1;
  }
  
  .price {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #F2F2F3;
  }

  .status-done {
    color: #00CCCC;
  }
  