.order {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 60px;
    width: 640px;
  }

  .number {
    display: flex;
    /* position: absolute; */
    margin: auto;
    margin-bottom: 20px;
    top: 50px;
    left: 40px;
  }
  
  .order-name {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  .status-done {
    color: #00CCCC;
  }
  
  .left-align {
    align-self: start;
  }
  
  .ingredients {
    list-style: none;
    display: flex;
    flex-direction: column;
    width: 640px;
    max-height: 312px;
    overflow: auto;
    margin: 0;
    padding: 0;
  }
  
  .ingredients::-webkit-scrollbar-thumb {
    height: 100px;
    background: #8585ad;
  }

  .ingredients::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background: #8585ad;
  }
 
  .ingredient {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .ingredient-preview {
    display: flex;
    align-items: center;
    flex-basis: 400px;
  }
  
  .price {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-right: 24px;
  }
  
  .order-price {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .image-box {
    width: 56px;
    height: 56px;
    background-color: #131316;
    background-position: center;
    background-size: 112px 56px;
    border: 2px solid var(--colors-interface-accent);
    padding: 2px;
    border-radius: 50%;
    flex-shrink: 0;
  }
  
  .content {
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .bottom-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
    width: 640px;
    margin-bottom: 50px;
  }
  